.contact{
   
  }
  
  .contactbg {
    background: url('../Images/telephone-612061.jpg') no-repeat center center;
    background-size: cover;
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;
  }
  
  .contacttext {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 300px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(-7px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    text-align: center;
  }
  
  .contacttext h2 {
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: 500;
    font-size: 35px;
    letter-spacing: 2px;
    line-height: 50px;
    text-align: center;
    text-transform: capitalize;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 0px;
    color: #000000bf;
    font-family: 'BebasNeue';
  }

  .contactform{
    padding-top: 5rem;
  }

  .contactform h2{
    color: #000000bf;
    margin: 80px;
    font-size: 35px;
    font-family: 'BebasNeue';
    margin: 0;
  }
  


  @media  (max-width: 2600px){
    
  

.contacttext {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 300px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(-7px);
  backdrop-filter: blur(-7px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 800px;
  text-align: center;
}
}
  